
import useVuelidate from "@vuelidate/core";
import { required, email, minLength } from "@vuelidate/validators";
import { db, firebase } from "@/firebase";
import { Router, useRouter } from "vue-router";
import { Store, useStore } from "vuex";

export function validName(name: string) {
  let validNamePattern = new RegExp("^[a-zA-Z]+(?:[-'\\s][a-zA-Z]+)*$");
  if (validNamePattern.test(name)) {
    return true;
  }
  return false;
}

export default {
  setup() {
    const router = useRouter();
    const store = useStore();

    return { v$: useVuelidate(), router, store };
  },

  data() {
    return {
      form: {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
      },
    };
  },

  methods: {
    signUpButtonPressed(e: Event) {
      console.log("Sign In Button Pressed");
      e.preventDefault();
    },
    async signUp(
      router: Router,
      store: Store<any>,
      form: {
        firstName: string;
        lastName: string;
        email: string;
        password: string;
      }
    ) {
      try {
        await firebase
          .auth()
          .setPersistence(firebase.auth.Auth.Persistence.LOCAL);
        const response = await firebase
          .auth()
          .createUserWithEmailAndPassword(form.email, form.password);
        if (response && response.user) {
          await response.user.updateProfile({
            displayName: `${form.firstName} ${form.lastName}`,
          });
          window.localStorage.setItem(
            `${process.env.VUE_APP_SITENAME}_refreshToken`,
            response.user.refreshToken
          );
          const dbUsers = db.collection("users");
          dbUsers.add({
            userUid: response.user.uid,
            date: firebase.firestore.FieldValue.serverTimestamp(),
            name: `${form.firstName} ${form.lastName}`,
            photo: "https://i.pravatar.cc/",
          });
          store.commit("setLogged", response.user);
          router.push("/");
        }
      } catch (error) {
        alert(error.message);
      }
    },
  },

  validations() {
    return {
      form: {
        firstName: {
          required,
          name_validation: {
            $validator: validName,
            $message:
              "Invalid Name. Valid name only contain letters, dashes (-) and spaces",
          },
        },
        lastName: {
          required,
          name_validation: {
            $validator: validName,
            $message:
              "Invalid Name. Valid name only contain letters, dashes (-) and spaces",
          },
        },
        email: { required, email },
        password: { required, min: minLength(6) },
      },
    };
  },
};
